import { Component, ChangeDetectionStrategy, ViewChild, ChangeDetectorRef } from "@angular/core";
import { Platform, Form, ViewController } from "ionic-angular";


import * as moment from "moment-mini";

import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsCreditCardComponent } from "@components/mars-credit-card/mars-credit-card";
import { MarsAuthService } from "@services/auth.service";
import { Backend } from "@backend/index";
import { MarsBankAccountCardComponent } from "@components/mars-bank-account-card/mars-bank-account-card";


@Component({
    selector: "page-add-bank-account",
    templateUrl: "add-bank-account.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class AddBankAccountPage {

    isMerchant: boolean;
    @ViewChild("form") form: any;
    @ViewChild("accountForm") accountForm: MarsBankAccountCardComponent;
    translations: AppTranslations;
    token: string;
    account: any = { holder: {} };

    constructor(private platform: Platform,
        private viewCtrl: ViewController,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private interactionService: MarsInteractionService,
        private globals: AppGlobals) {
        this.translations = this.locales.load();
    }

    ionViewWillEnter() {
        this.init();
    }

    async init() {
        if (MarsAuthService.isLoggedIn()) {
            this.isMerchant = MarsAuthService.hasRole('MERCHANT');
            this.token = MarsAuthService.getMarsToken();
            let holder = MarsAuthService.getLoggedInUser();
            this.account.type = "CHECKING";
            holder.taxDocument = holder.documents[0];
            this.account.holder = {
                taxDocument: holder.taxDocument
            };
            if (this.account.holder.taxDocument) delete this.account.holder.taxDocument._id;
        }
    }

    ionViewDidEnter() { }

    async save() {
        let spinner = this.interactionService.spinner({ content: this.translations.loading + "..." });
        try {
            let saved = await Backend.addBankAccount({ xAccessToken: this.token, account: this.account });
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
            spinner.dismiss();
        }
    }

    disableSubmit() {
        return this.form && !this.form.valid;
    }
}



